* {
  box-sizing: border-box;
}

html {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 20px;
  font-weight: 700;
}

body, article, span {
  margin: 0;
}

.people {
  grid-template-columns: repeat(2, 200px);
  justify-content: center;
  gap: 30px 40px;
  height: 100vh;
  padding-top: 30px;
  display: grid;
}

@media (width <= 500px) {
  .people {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 200px);
  }
}

.person {
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 231px;
  transition: all 1s;
  display: flex;
}

.person__details {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.person__picture {
  flex-direction: column;
  margin-bottom: 8px;
  display: flex;
  position: relative;
}

.person__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  line-height: 32px;
  position: absolute;
  inset: 84px 10px 84px 158px;
}

.person__photo {
  border-radius: 50%;
  width: 100%;
  height: 100%;
  transition: all 1s;
}

.person__photo:hover {
  border-radius: 60px;
}

.person__name {
  color: #2f4f4f;
  margin-bottom: 9px;
}

.person__extra {
  color: #000;
  opacity: 0;
  font-size: 18px;
  font-weight: 400;
}

.person:hover {
  background-color: #eee;
  border-radius: 12px;
  padding: 10px 10px 16px;
  transform: scale(1.2);
}

.person:hover .person__extra {
  opacity: 1;
}
